@font-face {
  font-family: Inter;
  src: url("https://fonts.googleapis.com/css2?family=Inter:wght@200;500&display=swap");
}

:root {
  --main-bg-color: #fff;
  --primary-color: #3369f3;
  --second-color: #fff;
  --main-text-color: #1e1e1e;
}

.layout {
  width: 100%;
  height: 100%;
  display: flex;
}

.block {
  width: 340px;
  min-height: 450px;
  background: var(--main-bg-color);
  border-radius: 12px;
  flex-flow: column;
  margin: auto;
  display: flex;
  top: 0;
  bottom: 0;
  box-shadow: 0 0 6px #00000024;
}

.block_title {
  text-align: center;
  color: var(--main-text-color);
  padding-top: 50px;
  font-family: Inter, sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
}

.block_body {
  flex-flow: column;
  flex: 1;
  margin-bottom: 30px;
  display: flex;
}

.block_footer {
  flex-flow: column;
  align-items: center;
  padding-bottom: 30px;
  display: flex;
}

.block_button {
  width: 280px;
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  margin: 0 auto;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.button__active {
  background: var(--primary-color);
  color: var(--second-color);
  height: 37px;
  border: 0;
  font-size: 13px;
  line-height: 37px;
}

.button__default {
  background: var(--second-color);
  color: var(--primary-color);
  margin-top: 14px;
  font-size: 11px;
}

.text:active, .text:hover, .text {
  color: #666;
  text-decoration: none;
}

.block_input {
  color: var(--main-text-color);
  height: 27px;
  width: 280px;
  border: 1px var(--primary-color);
  border-bottom-style: solid;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

:root {
  --main-bg-color: #fff;
  --primary-color: #3369f3;
  --attention-color: red;
  --second-color: #fff;
  --main-text-color: #1e1e1e;
  --secondary-text-color: #999;
  --border-color: #eaeaea;
}

.layout {
  width: 100%;
  height: 100%;
  display: flex;
}

.back {
  height: 100%;
  width: 64px;
  border: 1px var(--border-color);
  border-right-style: solid;
  display: flex;
}

.back_icon {
  height: 28px;
  width: 28px;
  margin: auto;
}

.profile {
  background-color: #fff;
}

.content_card {
  width: 510px;
  flex-flow: column;
  margin: auto;
  display: flex;
}

.card_title {
  text-align: center;
  color: var(--main-text-color);
  margin-top: 21px;
  margin-bottom: 60px;
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.card_field {
  border: 1px var(--border-color);
  height: 27px;
  border-bottom-style: solid;
  flex-flow: wrap;
  place-content: center space-between;
  margin-top: 10px;
  display: flex;
}

.card_field_name {
  text-align: left;
  color: var(--main-text-color);
  font-family: Inter, sans-serif;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
}

.card_field_value {
  text-align: right;
  color: var(--secondary-text-color);
  border: 0;
  font-family: Inter, sans-serif;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
}

.avatar {
  width: 130px;
  height: 130px;
  margin: auto;
}

.card_footer {
  flex-flow: column;
  margin-top: 60px;
  display: flex;
}

.footer_button {
  width: 100%;
  border: 1px var(--border-color);
  cursor: pointer;
  text-align: left;
  border: none;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.save_button {
  width: 280px;
  text-align: center;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  justify-content: center;
  margin: 0 auto;
  font-family: Inter, sans-serif;
  font-style: normal;
  font-weight: 500;
  display: flex;
}

.button_save__active {
  background: var(--primary-color);
  color: var(--main-bg-color);
  height: 37px;
  font-size: 13px;
  line-height: 37px;
}

.button_save__hide {
  display: none;
}

.button_prof__active {
  background: var(--main-bg-color);
  color: var(--primary-color);
  height: 37px;
  font-size: 13px;
  line-height: 37px;
}

.button__hide {
  display: none;
}

.button__red {
  background: var(--main-bg-color);
  color: var(--attention-color);
  font-size: 13px;
  line-height: 37px;
}

